import { Stack, Typography } from '@mui/material'
import { MailingApi } from 'API/mailing'
import DialogConfirm from 'components/DialogConfirm'
import { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'

export default function RemoveMail() {
	const nav = useNavigate()
	const { id, mId } = useParams<{ id: string; mId: string }>()
	const mailing = useAppSelector((s) => s.Mailing.list).find((f) => f.id === Number(id))
	const [loading, setLoading] = useState(false)
	const [open, setOpen] = useState(true)
	const dispatch = useAppDispatch()
	useEffect(() => {
		return () => {
			setOpen(false)
		}
	}, [])

	if (!mailing || !id || !mId) return null
	const member = mailing.mailMembers.find((f) => f.id === parseInt(mId))
	if (!member) return null
	const onSubmit = async () => {
		try {
			setLoading(true)
			const { error, message } = await MailingApi.removeMember({
				id,
				memberId: mId,
			})
			dispatch(
				UIAction.addGlobalMessage({
					type: error ? 'error' : 'success',
					message: message,
				})
			)
			if (!error) nav('/mailing/' + id)
		} finally {
			setLoading(false)
		}
	}
	return (
		<DialogConfirm
			open={open}
			loading={loading}
			handleClose={() => {
				nav('/mailing/' + id)
			}}
			title=""
			primaryButtonText="取り除く"
			primaryButtonColor="warning"
			maxWidth="sm"
			handleSubmit={onSubmit}
		>
			<Stack gap={1}>
				<Typography>メーリングリストから{member.email}を削除してもよろしいですか</Typography>
			</Stack>
		</DialogConfirm>
	)
}
